@import "reset.scss";

/* Style for Tag Input */
.tag-input {
  position: relative;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: .3em .5em;
  height: 2.2em;
  margin: 20px 0;
  cursor: text;
}
.tag {
  display: inline-block;
  padding: .2em .4em;
  margin-right: .3em;
  color: #333;
  background-color: lightblue;
  border-radius: .2em;
  font-size: .8em;
  font-weight: 300;

  .tag-remove {
    display: inline-block;
    color: #666;
    font-size: .7em;
    font-weight: 700;
    cursor: pointer;
  }
}
.tag-input {
  input {
    border: none;
    border-radius: 1em;
    margin-left: .2em;
    font-size: .8em;

    &:focus { outline: 0 }
  }

  .hidden {
    position: absolute;
    opacity: 0;
    left: 0;
  }
}

/* Style for Editor */
.md-panel {
  display: block;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.md-toolbar {
  position: absolute;
  width: 100%;
  height: 30px;
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 1px #ccc;
  z-index: 10;
}
.md-toolbar .tb-btn {

}
.md-editor {
  width: 50%;
  height: auto;
}
.md-editor textarea {
  border-style: none;
  resize: none;
  outline: 0;
  height: 100%;
  min-height: 420px;
  width: 100%;
  margin-top: 40px;
  padding: 0 15px;
}
.md-preview {
  position: absolute;
  width: 50%;
  height: 100%;
  left: 50%;
  top: 0;
  background-color: #f6f6f6;
  padding: 45px 20px 20px 20px;
  overflow: auto;
}
.md-spliter {
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  border-left: 1px solid #ccc;
  z-index: 105;
}
